.profile {
  font-family: 'Montserrat';
  text-align: center;
}
.profile section {
  text-align: center;
}
.profile .section-title {
  color: #817091;
  margin-top: 50px;
}
.profile .fa-star {
  cursor: pointer;
}


.profile svg {
  overflow: visible;
}
.profile .btn-edit {
  background-color: rgb(111, 216, 111);
  color: white;
  cursor: pointer;
  border-radius: 10px;
  padding: 20px 30px;
  display: inline-block;
  transition: .2s ease-in-out;
}
.profile .btn-edit:hover {
  box-shadow: 0 5px 5px #dcdcdc;
  top: -10px;
}
.profile .about-me {
  background-color: #F6F6F6;
  height: auto;
  overflow: auto; /* Collapsing Margins */
}
.profile .about-container {
  margin-bottom: 100px;
  display: block;
}
.profile .profile-avatar {
  border-radius: 50%;
  max-height: 350px;
  display: inline-block;
  margin-right: 100px;
  /* padding: 30px; */
  vertical-align: middle;
}
.profile .basic-info {
  padding-top: 30px;
  text-align: left;
  max-width: 210px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
  vertical-align: middle;
}
.profile .basic-info-nav, .profile .basic-info-text{
  display: block;
  text-align: left;
  padding: 0;
}
.profile .basic-info-nav {
  margin: 0;
}
.profile .basic-info-nav, .profile .basic-info-item {
  height: auto;
  margin-bottom: 30px;
}
.profile .basic-info-item {
  margin: 0 auto;
}
.profile .basic-info .basic-info-nav>li {
  display: block;
  text-align: left;
}
.profile .basic-info-title {
  color: #f197a3;
  text-transform: uppercase;
  font-size: 20px;
}
.profile .basic-info-text {
  font-size: 14px;
  color: #604f6f;
  font-weight: 300;
  opacity: 0.9;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-font-smoothing: antialiased
}
.profile .full-desc {
  margin: 0 auto;
  margin-top: 50px;
  text-align: left;
  max-width: 600px;
  font-weight: lighter;
  color: #604f6f;
  opacity: 0.94;
  -webkit-font-smoothing: antialiased;
}
.profile .full-desc strong {
  font-weight: bolder;
  -webkit-font-smoothing: auto;
}
@media (max-width: 670px) {
