    background-color: white;
}
.profile .section-title {
    font-size: 24px;
}
.profile .profile-avatar {
    max-height: 200px;
    margin-right: 10px;
    display: inline-block;
}
.profile .basic-info {
    display: inline-block;
}
.profile .basic-info-item {
    margin-bottom: 0;
}
.profile .basic-info-title {
    font-size: 14px;
}
.profile .basic-info-text {
    font-size: 9px;
    max-width: 150px;
}
}
@media (max-width: 400px) {
.profile .profile-avatar {
    max-height: 160px;
    margin-right: 10px;
    display: inline-block;
    max-width: 170px;
}
}

.profile svg path.progress {
  stroke: #f197a3;
  transition: .5s ease;
}
.profile .section-title {
  text-transform: uppercase;
  margin-bottom: 50px;
}
.profile .basic-info-item {
  margin-bottom: 40px;
}
.profile .skills {
  background-color: white;
  overflow: auto;
}
.profile .skill {
  margin: 0 auto;
  position: relative;
}
.profile .skill {
  max-width: 230px;
  margin-bottom: 50px;
}
.profile .skills-container {
  max-width: 800px;
  margin: 0 auto;
}
.profile .skill-container {
  text-align: center;
  vertical-align: middle;
  display: flex;
  align-items: center;
  position: relative;
}
.profile .skills .skill-title {
  color: #f197a3;
  text-transform: uppercase;
  font-size: 16px;
  margin-top: 25px;
  margin-bottom: 0;
}
.profile .skills .skill-percentage {
  color: #604f6f;
  font-size: 38px;
  position: absolute;
  transform: translateX(70%);
}
.profile .skills .skill-desc {
  font-family: 'Open Sans';
  color: #604f6f;
  white-space:normal;
  font-size: 0.8em;
  margin: 0 auto;
}

.profile .education-experience {
  background-color: #446594;
  color: white;
  height: auto;
  overflow: auto;
  -webkit-font-smoothing: antialiased;
}
.profile .education-experience h1.section-title {
  color: white;
  margin-bottom: 50px;
}
.profile .timeline {
  margin: 0 auto;
  position: relative;
  max-width: 1200px;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  align-content: space-between;
}
.profile .date-left {
  float: left;
  text-align: left;
  margin-left: 35px;
}
.profile .date-right {
  float: right;
  text-align: right;
  margin-right: 35px;
}
.profile .line-container {
  display: flex;
  justify-content: center;
}
.profile .ee-container {
  display: block;
  width: 100%;
  margin-bottom: 50px;
  height: 100px;
}
.timeline .icon {
  width: 50px;
  height: 50px;
  display: inline-block;
  background-color: white;
  border: 4px solid #f197a3;
  color: #f197a3;
  border-radius: 50%;
  padding: 10px;
}
.ee-container:first-of-type .line-container .icon::before {
  content: '';
  display: block;
  background-color: white;
  height: 30px;
  margin: 0 auto;
  margin-top: -50px;
  margin-bottom: 20px;
  width: 6px;
  border-radius: 20px;
}
.ee-container:last-child .line-container .icon::after {
  content: '';
  display: block;
  background-color: white;
  height: 35px;
  margin: 0 auto;
  margin-top: 15px;
  width: 6px;
  border-radius: 20px;
}
.timeline .icon::after {
  content: '';
  display: block;
  background-color: white;
  height: 90px;
  margin: 0 auto;
  margin-top: 15px;
  width: 6px;
  border-radius: 20px;
}
.timeline .icon .fa {
  font-size: 22px;
  transform:translate(-10%, 10%);
}
.timeline .container-title {
  font-size: 18px;
  text-transform: uppercase;
}
.timeline .container-subtitle {
  color: #ef8473;
  text-transform: uppercase;
  font-family: 'Open Sans';
  margin: 10px auto;
}
.timeline .info {
  width: 50%;
}
.timeline .info-left {
  text-align: left;
  float: right;
  margin-left: 35px;
}
.timeline .info-right {
  text-align: right;
  float: left;
  margin-right: 35px;
}
.timeline .container-desc, .timeline .container-location {
  color: #F4BAA5;
  font-size: 12px;
  font-weight: lighter;
  font-family: 'Open Sans';
  margin-bottom: 1px;
}
.profile .date-range-indication {
  width: 50%;
  display: inline-block;
}
.profile .education-experience .date-range {
  color: #446594;
  font-family: 'Montserrat';
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 30px;
  margin-bottom: 10px;
  padding: 18px 40px;
  display: inline-block;
  background-color: #f197a3;
  position: relative;
}
.profile .education-experience .date-range-indication .bubble {
  background-color: #f197a3;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  display: inline-block;
}
.date-left .bubble {
  margin-right: 35px;
}
.date-right .bubble {
  margin-left: 35px;
}
